ul.anticons-list {
  margin: 40px 0;
  list-style: none;
  overflow: hidden
}

ul.anticons-list li {
  float: left;
  width: 16.66%;
  text-align: center;
  list-style: none;
  cursor: pointer;
  height: 100px;
  color: #555;
  -webkit-transition: all .3s;
  transition: all .3s;
  position: relative;
  margin: 3px 0;
  border-radius: 4px;
  background-color: #fff;
  overflow: hidden;
  padding: 10px 0 0
}

ul.anticons-list li .anticon {
  font-size: 24px;
  margin: 12px 0 16px;
  -webkit-transition: all .3s;
  transition: all .3s
}

ul.anticons-list li .anticon-class {
  display: block;
  text-align: center;
  -webkit-transform: scale(.83);
  -ms-transform: scale(.83);
  transform: scale(.83);
  font-family: Lucida Console, Consolas;
  white-space: nowrap
}

ul.anticons-list li:hover {
  background-color: #108ee9;
  color: #fff
}

ul.anticons-list li:hover .anticon {
  -webkit-transform: scale(1.4);
  -ms-transform: scale(1.4);
  transform: scale(1.4)
}

ul.anticons-list li.copied:hover {
  color: hsla(0, 0%, 100%, .2)
}

ul.anticons-list li:after {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  content: "Copied!";
  text-align: center;
  line-height: 110px;
  color: #fff;
  -webkit-transition: all .3s cubic-bezier(.18, .89, .32, 1.28);
  transition: all .3s cubic-bezier(.18, .89, .32, 1.28);
  opacity: 0
}

ul.anticons-list li.copied:after {
  opacity: 1;
  top: -10px
}

.browser-mockup {
  border-top: 2em solid hsla(0, 0%, 90%, .7);
  box-shadow: 0 .1em .5em 0 rgba(0, 0, 0, .28);
  position: relative;
  border-radius: 3px 3px 0 0
}

.browser-mockup:before {
  display: block;
  position: absolute;
  content: "";
  top: -1.25em;
  left: 1em;
  width: .5em;
  height: .5em;
  border-radius: 50%;
  background-color: #f44;
  box-shadow: 0 0 0 2px #f44, 1.5em 0 0 2px #9b3, 3em 0 0 2px #fb5
}

.browser-mockup.with-tab:after {
  display: block;
  position: absolute;
  content: "";
  top: -2em;
  left: 5.5em;
  width: 20%;
  height: 0;
  border-bottom: 2em solid #fff;
  border-left: .8em solid transparent;
  border-right: .8em solid transparent
}

.browser-mockup.with-url:after {
  display: block;
  position: absolute;
  content: "";
  top: -1.6em;
  left: 5.5em;
  width: calc(100% - 6em);
  height: 1.2em;
  border-radius: 2px;
  background-color: #fff
}

.browser-mockup>* {
  display: block
}

