    margin-right: auto;
    float: none;
    display: block
  }
  .banner-entry {
    position: relative;
    top: 30px;
    left: 0;
    text-align: center
  }
  .image-wrapper {
    display: none
  }
  .banner-wrapper {
    background-position: 40%
  }
  .content-wrapper .text-wrapper {
    float: none;
    text-align: center;
    left: 0;
    width: 100%;
    padding: 0
  }
  .content-wrapper .text-wrapper>p {
    max-width: 100%!important;
    padding: 0 40px
  }
  .content-wrapper.page {
    min-height: 300px;
    height: 300px
  }
  .banner-text-wrapper {
    left: 50%;
    -webkit-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    transform: translateX(-50%);
    text-align: center
  }
  .banner-text-wrapper .start-button {
    text-align: center
  }
  .banner-text-wrapper .start-button>a {
    margin: 0 4px
  }
  .banner-text-wrapper .github-btn {
    text-align: center;
    float: none;
    display: inline-block
  }
  .banner-text-wrapper .line {
    display: none
  }
  button.lang {
    color: rgba(0, 0, 0, .65);
    border-color: rgba(0, 0, 0, .65)
  }
  button.lang, div.version {
    display: block;
    margin: 29px auto 16px
  }
  div.version>.ant-select-selection {
    color: rgba(0, 0, 0, .65)
  }
  div.version>.ant-select-selection:not(:hover) {
    border-color: rgba(0, 0, 0, .65)
  }
  .popover-menu {
    width: 300px
  }
  .popover-menu button.lang {
    margin: 16px auto;
    float: none
  }
  .popover-menu div.version {
    margin: 32px auto 16px;
    float: none
  }
  .popover-menu .ant-popover-inner {
    overflow: hidden
  }
  .popover-menu .ant-popover-inner-content {
    padding: 0
  }
  ul#nav, ul#nav li {
    width: 100%;
    font-size: 14px
  }
  ul#nav li {
    line-height: 60px;
    height: 60px;
    padding: 0!important;
    border: 0
  }
  ul#nav li a {
    color: #333
  }
  .toc {
    display: none
  }
  .nav-phone-icon {
    display: block
  }
  .nav-phone-icon:before {
    content: "";
    display: block;
    border-radius: 2px;
    width: 16px;
    height: 2px;
    background: #777;
    box-shadow: 0 6px 0 0 #777, 0 12px 0 0 #777;
    position: absolute
  }
  .main {
    height: 14%
  }
  .aside-container {
    float: none;
    width: auto;
    padding-bottom: 30px;
    border-bottom: 1px solid #e9e9e9;
    border-right: 0;
    margin-bottom: 30px
  }
  .main-container {
    padding-left: 16px;
    padding-right: 16px;
    margin-right: 0
  }
  .main-container>.markdown>* {
    width: 100%!important
  }
  .main-wrapper {
    width: 100%;
    border-radius: 0;
    margin: 0
  }
  footer {
    text-align: center
  }
  footer ul li {
    float: none;
    width: auto
  }
  footer ul li>h2 .anticon {
    display: none
  }
}
